@import '../common/styles/sass/mixins';
@import '../common/styles/sass/variables';

.header {
    @include flexStyle(inherit,inherit,center);
    padding: .5rem 1rem;
    border-color: transparent;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 9;
    .container {
        width: 80%;
        height: 100%;
        margin: 0 ;
        @include flexStyle(row, inherit, center);
    }
}


